.photos__item {
  position: relative;
  display: grid;
  grid-template-areas:
    "element__image element__image"
    "element__title element__favorite"
    "element__title element__likes";
  grid-auto-columns: auto min-content min-content;
  grid-auto-rows: 282px min-content min-content;
  grid-column-gap: 15px;
  padding: 0 0 25px;
  background-color: #fff;
  border-radius: 10px;
  align-items: center;
}
