.profile__edit-button-avatar {
  position: absolute;
  top: 0;
  right: 0;
  padding: 0;
  border: 0;
  outline: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  background: center/contain no-repeat
    url(../../../images/profile-avatar-edit-button.svg);
  background-size: 26px;
}

.profile__edit-button-avatar:hover {
  opacity: 1;
  cursor: pointer;
  transition: opacity 0.2s linear;
}
