.photos__favorite {
  grid-area: element__favorite;
  margin: 27px 15px 0 0;
  padding: 0;
  outline: none;
  border: 0;
  width: 21px;
  height: 18px;
  background: center/contain no-repeat url(../../../images/favorite.svg);
}

.photos__favorite:hover {
  opacity: 0.6;
  cursor: pointer;
  transition: opacity 0.2s linear;
}
