.popup__close-icon {
  position: absolute;
  top: -33px;
  right: -33px;
  width: 40px;
  height: 40px;
  outline: 0;
  border: 0;
  padding: 0;
  background: center/cover no-repeat url(../../../images/close-icon.svg);
  background-size: 100% 100%;
}

.popup__close-icon:hover {
  opacity: 0.6;
  transition: opacity 0.2s linear;
  cursor: pointer;
}

@media (max-width: 767px) {
  .popup__close-icon {
    top: -25px;
    right: -25px;
    width: 30px;
    height: 30px;
  }
}
