.popup {
  opacity: 0;
  position: fixed;
  transition: visibility 0.5s, opacity 0.5s;
  visibility: hidden;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}
