.popup__input {
  font-size: 14px;
  line-height: 17px;
  outline: none;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  width: 100%;
  display: block;
  padding: 13px 0;
  margin-bottom: 0;
}

.popup__input:last-of-type {
  /*margin-top: 30px;*/
  margin-top: 0;
  margin-bottom: 0;
}

@media (max-width: 767px) {
  .popup__input {
    font-size: 12px;
    padding: 11px 0;
  }

  .popup__input:last-of-type {
    margin-top: 0;
    /*margin-top: 16px;*/
    margin-bottom: 0;
  }
}
