.popup__save-button {
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  background: #000;
  border-radius: 2px;
  width: 100%;
  color: #fff;
  display: block;
  /*margin-top: 48px;*/
  margin-top: 0;
  padding: 13px 0;
  outline: none;
  border: 0;
}

.popup__save-button:hover {
  background-color: rgba(0, 0, 0, 0.8);
  transition: background-color 0.2s linear;
  cursor: pointer;
}

@media (max-width: 767px) {
  .popup__save-button {
    margin-top: 0;
    /*margin-top: 36px;*/
    padding: 10px 0;
    font-size: 16px;
  }
}
