.popup__save-button_inactive {
  border: 1px solid #c4c4c4;
  color: #c4c4c4;
  box-sizing: border-box;
  border-radius: 2px;
  background: #fff;
}

.popup__save-button_inactive:hover {
  border: 1px solid #c4c4c4;
  color: #c4c4c4;
  box-sizing: border-box;
  border-radius: 2px;
  background: #fff;
  cursor: default;
}
